@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../../fonts/PTMono-Regular.woff2") format("woff2"), url("../../fonts/PTMono-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.page-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.page-wrapper .main-header,
.page-wrapper .main-footer {
  flex-shrink: 0;
}
.page-wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.button {
  cursor: pointer;
  text-transform: uppercase;
  border-width: 1px;
  border-style: solid;
  border-radius: 3px;
  font-weight: 700;
}

.main-header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  position: fixed;
  width: 100%;
}

.main-header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  max-width: 1160px;
  min-height: 120px;
  margin: 0 auto;
}

.main-header__contacts-list {
  max-width: 685px;
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
}

.main-header__contacts-item {
  font-family: "PT Mono", "Courier New", monospace;
  list-style: none;
  margin-left: 30px;
}
.main-header__contacts-item:first-child {
  margin-left: 0;
}

.main-header__contacts-link {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #f2f5f7;
  font-size: 16px;
  line-height: 18px;
  fill: #00b2ff;
  padding-top: 20px;
  padding-bottom: 20px;
}
.main-header__contacts-link:hover {
  color: #00b2ff;
}
.main-header__contacts-link:focus, .main-header__contacts-link:focus-within {
  color: #00b2ff;
  outline: none;
}
.main-header__contacts-link:active {
  fill: rgba(0, 178, 255, 0.7);
  color: rgba(0, 178, 255, 0.7);
}

.main-header__contacts-link svg {
  margin-right: 15px;
  fill: inherit;
}

.main-header__button {
  max-width: 220px;
  padding: 10px 15px;
  color: #f2f5f7;
  background-color: transparent;
  border-color: #f2f5f7;
}
.main-header__button:hover {
  background-color: #00b2ff;
  border-color: transparent;
}
.main-header__button:focus, .main-header__button:focus-within {
  background-color: #00b2ff;
  border-color: transparent;
  outline: none;
}
.main-header__button:active {
  background-color: rgba(0, 178, 255, 0.7);
}

@media (max-width: 1023px) {
  .main-header {
    position: static;
  }
  .main-header__wrapper {
    width: 84%;
    max-width: 650px;
    min-height: 100px;
  }
  .main-header__contacts-list {
    max-width: 285px;
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0;
  }
  .main-header__contacts-item {
    margin-left: 20px;
  }
  .main-header__contacts-link {
    padding: 10px;
  }
  .main-header__contacts-link svg {
    margin: 0;
  }
  .main-header__contacts-link span {
    display: none;
  }
  .main-header__button {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    width: 81%;
    max-width: 400px;
    min-height: 70px;
  }
  .main-header__contacts-list {
    max-width: 185px;
  }
  .main-header__logo img {
    width: 35px;
    height: 35px;
  }
}
.main-footer {
  background-color: #041827;
}

.main-footer__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 1220px;
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 55px;
}

.main-footer__socials-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-footer__logo {
  margin-bottom: 30px;
}

.main-footer__nav-wrapper {
  max-width: 470px;
}

.main-footer__title {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
}

.socials__list {
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: 40px 40px 40px;
  grid-template-rows: 40px;
  gap: 15px;
  list-style: none;
}

.socials__link {
  fill: #f8fdff;
  color: inherit;
}
.socials__link:hover {
  fill: #00b2ff;
}
.socials__link:focus, .socials__link:focus-within {
  fill: #00b2ff;
  outline: none;
}
.socials__link:active {
  fill: rgba(0, 178, 255, 0.7);
}

.socials__link svg {
  fill: inherit;
}

.footer-nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.footer-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer-nav__item {
  margin-bottom: 10px;
}
.footer-nav__item:last-child {
  margin-bottom: 0;
}

.footer-nav__link {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}
.footer-nav__link:hover {
  color: #00b2ff;
}
.footer-nav__link:focus, .footer-nav__link:focus-within {
  color: #00b2ff;
  outline: none;
}
.footer-nav__link:active {
  color: rgba(0, 178, 255, 0.7);
}

.modal {
  display: none;
}

.footer-contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer-contacts__item {
  width: 345px;
  margin-bottom: 10px;
}
.footer-contacts__item:last-child {
  margin-bottom: 0;
}

.footer-contacts__link {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.7);
}
.footer-contacts__link:hover {
  color: #00b2ff;
}
.footer-contacts__link:focus, .footer-contacts__link:focus-within {
  color: #00b2ff;
  outline: none;
}
.footer-contacts__link:active {
  color: rgba(0, 178, 255, 0.7);
}

